    color: #86878B;
    font-size: 18px;
    line-height: 21px;
}

.media__list{
    display: flex;
    align-items: center;
}

.list{
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.list__item{
    height: 100%;
    width: 100%;
    margin-right: 45px;
}

.list__link--facebook{
    background-image: url(../assets/svg/facebook.svg);
}

.list__link--twitter{
    background-image: url(../assets/svg/twitter.svg);
}

.list__link--linkedin{
    background-image: url(../assets/svg/linkedin.svg);
}

.list__link--pinterest{
    background-image: url(../assets/svg/pinterest.svg);
}

.list__link--instagram{
    background-image: url(../assets/svg/instagram.svg);
}

