    width: 50px;
    height: 50px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    transition: .3s linear;
}

.list__link:hover{
    background-color: #EC7979;
}

.list__link:focus{
    background-color:rgb(230, 139, 109);
}


